<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <el-card>
            <el-row :gutter="20">
                <el-col :span="16"><div class="grid-content bg-purple">
                    <el-card>
                        <el-row :gutter="20">
                            <el-col :span="5"><div class="grid-content bg-purple">
                                <el-dropdown split-button type="primary" @command="handleCommand">{{queryInfo.style}}
                                    <el-dropdown-menu slot="dropdown">
                                        <el-dropdown-item command="1">id</el-dropdown-item>
                                        <el-dropdown-item command="2"></el-dropdown-item>
                                        <el-dropdown-item command="3"></el-dropdown-item>
                                    </el-dropdown-menu>
                                </el-dropdown>
                            </div></el-col>
                            <el-col :span="10"><div class="grid-content bg-purple">
                                <!--  -->
                                <el-input style="margin-left: -30px" placeholder="" v-model="queryInfo.query" clearable @clear="">
                                    <el-button slot="append" icon="el-icon-search" @click=""></el-button>
                                </el-input>
                            </div></el-col>
                        </el-row>
                        <!--  -->
                        <el-table :data="vacclist" height="500" border stripe>
                            <el-table-column label="" width="120px" prop="name"></el-table-column>
                            <el-table-column label="id" width="80px" prop="rootid"></el-table-column>
                            <el-table-column label="" width="90px" prop="vaccid"></el-table-column>
                            <el-table-column label="" width="80px" prop="data3"></el-table-column>
                            <el-table-column label="" width="90px" prop="because"></el-table-column>
                            <el-table-column label=":"  width="80px"  prop="isadmin">
                                <template slot-scope="scope">
                                    <el-tag  v-if="scope.row.isback" type="warning"></el-tag>
                                    <el-tag  v-if="scope.row.isout" type="success"></el-tag>
                                </template>
                            </el-table-column>
                        </el-table>
                        <!--  -->
                        <el-pagination
                                @size-change="handleSizeChange"
                                @current-change="handleCurrentChange"
                                :current-page="queryInfo.pageNum"
                                :page-sizes="[10, 20, 40, 80]"
                                :page-size="queryInfo.pageSize"
                                layout="total, sizes, prev, pager, next, jumper"
                                :total="total"
                        ></el-pagination>
                    </el-card>
                </div></el-col>
                <el-col :span="8"><div class="grid-content bg-purple">
                   <el-card>

                   </el-card>
                </div></el-col>
            </el-row>
        </el-card>
    </div>
</template>


<style lang="less" scoped>
</style>
<script>
    export default {
        data(){
            return {
                queryInfo: {
                    query: "",
                    pageNum: 1,
                    pageSize: 20,
                    style:'',
                },
                vacclist: [],// 
                total: 0, // 
            }

        },
        created() {
            this.getVaccloglist();
        },
        methods: {
            handleCommand(command) {
                if(command==1){
                    this.queryInfo.style="id"
                }
                if(command==2){
                    this.queryInfo.style=""
                }
                if(command==3){
                    this.queryInfo.style=""
                }
            },
            //
            // pageSize
            handleSizeChange(newSize) {
                this.queryInfo.pageSize = newSize;
                this.getVaccloglist(); // 
            },
            // pageNum
            handleCurrentChange(newPage) {
                this.queryInfo.pageNum = newPage;
                this.getVaccloglist();// 
            },
          async  getVaccloglist(){
                const {data:res} = await this.$http.post("api/vacc/log",this.queryInfo);
                if(res!=null){
                   this.vacclist = res.data;
                   this.total = res.number;
                }
            }
        },
    }

</script>