<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
    </div>
</template>
<script>
    import * as echarts from 'echarts';//echarts
    export default {
        data() {
            return {

            };
        },
        created() {

        },
        mounted() {
           // this.getEchartdata();
        },
        methods: {

            //
            async  getEchartdata(){
                const { data: res } = await this.$http.post("api/room/echartdata"); //api/echartdata

                if(res!=null){
                    //,,,
                    var xdata = res[0].split(","); //resxdata
                    var ydata = res[1].split(","); //resydata
                }
                var chartDom = document.getElementById('myechart');
                var myChart = echarts.init(chartDom);
                var option;
                option = {
                    title: {
                        text: '',
                        subtext: ''
                    },
                    xAxis: {
                        type: 'category',
                        data: xdata,
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [
                        {
                            data: ydata,
                            type: 'bar'
                        }
                    ]
                };
                option && myChart.setOption(option);
            },

        }
    };
</script>
